  "2024_Sri_Lankan_presidential_election":
    "Presidential elections are scheduled to be held in Sri Lanka sometime between September and October 2024, according to the Constitution of Sri Lanka. Voters will elect a president for a term of five years. Incumbent president Ranil Wickremesinghe is eligible to run for re-election. ",

  // 2019_Sri_Lankan_presidential_election
  "2019_Sri_Lankan_presidential_election":
    "Presidential elections were held in Sri Lanka on 16 November 2019. The incumbent President Maithripala Sirisena's term of office would have ended on 9 January 2020. This was the first presidential election in Sri Lanka where no sitting president, prime minister or opposition leader ran for president. Sajith Premadasa, son of former president Ranasinghe Premadasa and deputy leader of the United National Party was the candidate of the ruling party. Gotabaya Rajapaksa, brother of former president Mahinda Rajapaksa, was the candidate of the Sri Lanka Podujana Peramuna and was endorsed by the Sri Lanka Freedom Party. The results were announced on 17 November 2019. Rajapaksa won the election in a landslide victory, defeating his main opponent Premadasa by a significant margin, thus bringing the Rajapaksa family back to power after a 5-year interregnum. ",

  // 2015_Sri_Lankan_presidential_election
  "2015_Sri_Lankan_presidential_election":
    "Presidential elections were held in Sri Lanka on 8 January 2015, two years ahead of schedule. Incumbent President Mahinda Rajapaksa was the ruling United People's Freedom Alliance's candidate, seeking a third term in office. The United National Party (UNP)-led opposition coalition chose to field Maithripala Sirisena, the former Minister of Health in Rajapaksa's government and general secretary of the Sri Lanka Freedom Party (SLFP) \u2013 the main constituent party of the UPFA \u2013 as its common candidate. Sirisena was declared the winner, receiving 51. 28% of all votes cast compared to Rajapaksa's 47. 58%. The result was generally seen as a major upset; when Rajapaksa called the election in November 2014 he had looked certain to win. On 11 January 2015, the new government announced a special investigation into allegations of an attempted coup by Rajapaksa. ",

  // 2010_Sri_Lankan_presidential_election
  "2010_Sri_Lankan_presidential_election":
    "Presidential elections were held in Sri Lanka on 26 January 2010. The elections were announced on 23 November 2009 when incumbent President Mahinda Rajapaksa decided to seek a fresh mandate prior to the expiration of his term in 2011. Nominations were accepted on 17 December 2009. Rajapaksa, who was elected president for a 6-year term in November 2005, was the candidate of the ruling United People's Freedom Alliance. General Sarath Fonseka, a former commander of the Sri Lankan Army, was his main opponent in the election. Fonseka had been endorsed by a number of main opposition parties, including the United National Party and the Janatha Vimukthi Peramuna. President Rajapaksa proceeded to win re-election, over 57% of all votes cast. Fonseka received over 40%, carrying the Northern and Eastern Provinces of Sri Lanka. The remaining twenty candidates all garnered less than 0. 5% of the popular vote. ",

