  // 2020_Sri_Lankan_parliamentary_election
  "2020_Sri_Lankan_parliamentary_election":
    "Parliamentary elections were held in Sri Lanka on 5 August 2020 to elect 225 members to Sri Lanka's 16th Parliament. 16,263,885 people were eligible to vote in the election, 31. 95% of whom were young voters. The incumbent Sri Lanka People's Freedom Alliance coalition claimed a landslide victory in the election, winning 145 seats, while Samagi Jana Balawegaya won 54 seats, Tamil National Alliance won 10 seats and National People's Power won 3 seats. The main opposition United National Party suffered the worst showing in its history following a split over party leadership, finishing in fourth place with only one seat. The election was postponed at least twice due to a surge in COVID-19 cases in the country, before the date was finalized as 5 August 2020. Prior to the election, a coronavirus-proof mock election was conducted by the Election Commission in June 2020 as a trial run in order to comply with health guidelines. Initial reports revealed that the overall voter turnout was estimated at 70%, comparatively less than the turnout of the previous elections, mainly due COVID-19. Vote counting started on 6 August 2020 at around 9 am. The new parliament is expected to reconvene on 20 August 2020. In November 2018, the date was briefly moved forward by more than a year to 5 January 2019 after President Maithripala Sirisena dissolved parliament during a constitutional crisis and called for a snap election. The Supreme Court later suspended the dissolution and ordered a halt to the snap election, effectively moving the election's date back to 2020. The next general election in Sri Lanka is scheduled to be held in 2025; however, the president has the power to dissolve parliament anytime after 2+1\u20442 years according to the constitution. ",

  // 2015_Sri_Lankan_parliamentary_election
  "2015_Sri_Lankan_parliamentary_election":
    "Parliamentary elections were held in Sri Lanka on 17 August 2015, ten months ahead of schedule, to elect 225 members to Sri Lanka's 15th Parliament. The United National Party (UNP) led United National Front for Good Governance (UNFGG) won 106 seats, an increase of 46 since the 2010 election, but failed to secure a majority in Parliament. The United People's Freedom Alliance (UPFA) won 95 seats, a decline of 49. The Tamil National Alliance (TNA), the largest party representing Sri Lankan Tamils, won 16 seats, an increase of two from 2010. The remaining eight seats were won by Janatha Vimukthi Peramuna (6), Sri Lanka Muslim Congress (1) and Eelam People's Democratic Party (1). Prime Minister Ranil Wickremesinghe, leader of the UNFGG and UNP, was able to form a national government with the support of UPFA MPs loyal to President Maithripala Sirisena. ",

  // 2010_Sri_Lankan_parliamentary_election
  "2010_Sri_Lankan_parliamentary_election":
    "Parliamentary elections were held in Sri Lanka on 8 and 20 April 2010, to elect 225 members to Sri Lanka's 14th Parliament. 14,088,500 Sri Lankans were eligible to vote in the election at 11,102 polling stations. It was the first general election to be held in Sri Lanka following the conclusion of the civil war which lasted 26 years. The main parties contesting in the election were the ruling United People's Freedom Alliance (UPFA), the main opposition United National Front (UNF) and the Democratic National Alliance (DNA) led by former commander of the Sri Lankan Army Sarath Fonseka. President Mahinda Rajapaksa had previously been reelected as president in January 2010. As expected, the UPFA secured a landslide victory in the elections, buoyed by its achievement of ending the 30 year Sri Lankan Civil War and defeating the Liberation Tigers of Tamil Eelam in May 2009. The UPFA won a large majority in the parliament, obtaining 144 seats, an increase of 39 since the 2004 election. The main opposition UNF won 60 seats, a decline of 22. The minority Tamil party Tamil National Alliance (TNA) won 14 seats, down from the 22 they won in 2004, and the DNA, contesting for the first time, won 7 seats. The UPFA however fell short of its goal of obtaining a two-thirds supermajority in the house, which it would have needed to change the constitution on its own. The election had the lowest voter turnout in Sri Lanka since independence. While the elections were initially scheduled to be concluded on 8 April, irregularities in two districts led the Commissioner of Elections to hold re-polls on 20 April. Final results were announced the following day, a day before the new parliament was scheduled to meet for the first time. ",

